import React, {useState,useEffect} from "react";
import PropTypes from "prop-types";
import keycode from "keycode";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableFooter from "@material-ui/core/TableFooter";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import CardEditMenu from './CardEditMenu.js'
import SearchBox from 'components/SearchBox/index';
import {base_url, public_url, $http} from "config";
import {useHistory} from 'react-router-dom';
import MenuItem from '@material-ui/core/MenuItem';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import {useSelector} from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import {Modal, ModalHeader} from 'reactstrap';
import CloseIcon from '@material-ui/icons/Close';
import Fab from '@material-ui/core/Fab';
import Avatar from '@material-ui/core/Avatar';
import SweetAlert from 'react-bootstrap-sweetalert';

const columnData = [
  {id: "category_name", align: false, disablePadding: true, label: "Category Name"},
  {id: "images", align: true, disablePadding: false, label: "Images"},
  {id: "status", align: true, disablePadding: false, label: "Description"},
  {id: "Actions", align: true, disablePadding: false, numeric:"right", label: "Actions"}
];

const DataTableHead = (props) => {

  const createSortHandler = property => event => {
    props.onRequestSort(event, property);
  };

    const {order, orderBy} = props;

    return (
      <TableHead>
        <TableRow>
          {columnData.map(column => {
            return (
              <TableCell
                key={column.id}
                align={column.numeric}
              >
                <Tooltip
                  title="Sort"
                  placement={column.numeric ? "bottom-end" : "bottom-start"}
                  enterDelay={300}
                >
                  <TableSortLabel
                    active={orderBy === column.id}
                    direction={order}
                    onClick={() => createSortHandler(column.id)}
                  >
                    {column.label}
                  </TableSortLabel>
                </Tooltip>
              </TableCell>
            );
          })}
        </TableRow>
      </TableHead>
    );
  };

  DataTableHead.propTypes = {
    onRequestSort: PropTypes.func.isRequired,
    order: PropTypes.string.isRequired,
    orderBy: PropTypes.string.isRequired,
  };


  let DataTableToolbar = (props) => {

    const {handleOpenClick, handleCatOpenClick} = props;
  return (
    <Toolbar className="table-header">
      <div className="title">
          <Typography variant="h6">Categories</Typography>
      </div>
      <div className="col-md-3 col-lg-3 col-sx-6 col-6 ml-auto">
          <SearchBox placeholder="Search ..."/>
      </div>
      <div className="actions">
        <Tooltip title="Add Sticker" onClick={handleOpenClick}>
          <IconButton className="icon-btn">
            <i className="zmdi zmdi-collection-image"/>
          </IconButton>
        </Tooltip>
        <Tooltip title="Add Category" onClick={handleCatOpenClick}>
          <IconButton className="icon-btn">
            <i className="zmdi zmdi-collection-plus"/>
          </IconButton>
        </Tooltip>
      </div>
    </Toolbar>
  );
};


const CategoryTable = () => {

  const History = useHistory();

  const [loader, setLoader] = useState(false);
  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState("id");
  const [selected, setSelected] = useState([]);
  const [category_data, setData] = useState([]);
  const [open, setOpen] = useState(false);
  const [cat_open, setCatOpen] = useState(false);
  const [add_img, setAddImg] = useState(null);
  const [ImageUrl, setImageUrl] = useState('https://via.placeholder.com/300x300');
  const [CategoryImgUrl, setCategoryImgUrl] = useState('https://via.placeholder.com/300x300');
  const [category_img, setCategoryImg] = useState(null);
  const [category, setCategory] = useState("");
  const [img_name, setImgName] = useState("");
  const [categories, setCategories] = useState([])
  const [status, setStatus] = useState("");
  const status_rows = ([
        {id: 0, content: 'Deactive'},
        {id: 1, content: 'Active'}
    ]);

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const {width} = useSelector(({settings}) => settings);

  const [category_id, setCategoryId] = useState(0);
  const [goback, setGoback] = useState(false)

  useEffect(() => {
    init_Data();
  }, []);

  const init_Data = () => {
    $http.get(base_url + "category/categoryList")
    .then((response) => {
      setData(response.data.category);
      setLoader(true);
    }).catch((error) => {
      setGoback(true)
    })
  }

  const handleRequestSort = (event, property) => {
    let sort_order;
    if (orderBy === property && order === "asc") {
      sort_order = "desc";
    } else {
      sort_order = "asc";
    }
    sort_order === "desc"
      ? category_data.sort((a, b) => (b[property] < a[property] ? -1 : 1))
      : category_data.sort((a, b) => (a[property] < b[property] ? -1 : 1));

    setOrder(sort_order);
    setOrderBy(property);
  };


  const handleClick = (id) => {
    History.push({
      pathname: "/app/stickers/categories/info",
      state: {id: id}
    })
  };

 

  const handleChangePage = (event, page) => {
    setPage(page);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(event.target.value);
  };

  const onSelectCategory = (data, index) => {
    if (index === 0) {
      History.push({
        pathname: "/app/stickers/categories/info",
        state: {id: data}
      })
    } else if (index === 1) {
      handleCatOpenClick(data);
    } else if (index === 2) {
      const frmData = new FormData();
      frmData.append('id', data);
      $http.post(base_url + "category/deleteCategory",frmData)
      .then((response) => {
        if (response.data.status === "success") {
          if(navigator.onLine)
              init_Data();
        } else if (response.data.status === "error") {
            NotificationManager.error(response.data.msg, "Error!", 1000, () => {
            });
        }
      }).catch((error)=>{
        NotificationManager.error(error, "Error!", 1000, () => {
        });
      })
      if(!navigator.onLine){
        caches.open('SirvezApp').then(function(cache){
          cache.match('/api/room/editPhoto')
          .then(function(response) {return response ? response.json() : null})
          .then(function(responseData){
            if(responseData == null) return
            for (var i = 0; i < responseData.categories.length; i++) {
              if (responseData.category[i].id == data) {
                responseData.category.splice(i,1)
                const jsonResponse = new Response(JSON.stringify(responseData), {
                  headers: {
                      'content-type': 'application/json'
                  }
                });
                cache.put('/api/room/editPhoto', jsonResponse.clone())
                .then(function(result){
                  init_Data()
                })
                break;
              }
            }
          })

          cache.match('/api/category/categoryList')
          .then(function(response) {return response ? response.json() : null})
          .then(function(responseData){
            if (responseData == null) return
            for (var i = 0; i < responseData.category.length; i++) {
              if (responseData.category[i].id == data) {
                responseData.category.splice(i,1)
                const jsonResponse = new Response(JSON.stringify(responseData), {
                  headers: {
                      'content-type': 'application/json'
                  }
                });
                cache.put('/api/category/categoryList', jsonResponse.clone())
                .then(function(result){
                  init_Data()
                })
                break;
              }
            }
          })
        })
      }

    }
  }

  const [add_category, setAddCategory] = useState("");
  const [add_category_des, setAddCategoryDes] = useState("");

  const handleOpenClick = () => {
      $http.get(base_url + "category/categoryList")
      .then((response) => {
        setCategories(response.data.category)
          setOpen(true);
      }).catch((error) => {
        setGoback(true)
      })
  }

  const handleCloseClick = () => {
      setOpen(false);
  }

  const AddImageClick = () => {
    handleCloseClick();
    let offline_id = JSON.parse(localStorage.getItem('user')).first_name+'_'+new Date().getTime();
    const frmData = new FormData();
    frmData.append('id', offline_id);
    frmData.append('stiker_img', add_img);
    frmData.append('name', img_name);
    frmData.append('category_id', category);
    frmData.append('status', status);

    $http.post(base_url + "sticker/updateSticker", frmData)
    .then((response) => {
        if (response.data.status === "success") {
            NotificationManager.success("Update Sticker", "Success!", 1000, () => {
            });
            if(navigator.onLine)
              init_Data();
        } else if (response.data.status === "error") {
            NotificationManager.error(response.data.msg, "Error!", 1000, () => {
            });
        }
    }).catch((error)=>{
      NotificationManager.error(error, "Error!", 1000, () => {
      });
    })

    if (!navigator.onLine){
      caches.open('SirvezApp').then(function(cache){
          let fileName = ''
          if(add_img)
          {
              fileName = new Date().getTime()+'_new.jpeg';
              cache.put('/pixie/assets/images/stickers/'+ category + '/'+fileName, new Response(add_img));
          }
          var modify_data = {
              id : offline_id,
              name : img_name,
              category_id : category,
              stiker_img : fileName,
              
          }   
          //save category_edit
          
            let res_data = {stiker:modify_data}
            const j_Response = new Response(JSON.stringify(res_data), {
                headers: {
                    'content-type': 'application/json'
                }
            });
            cache.put('/api/category/sticker/getStickerInfo?id='+offline_id, j_Response.clone());
          
            
          //save into list
          const requestUrl = '/api/category/getCategoryInfo?id='+category_id;
          cache.match(requestUrl)
          .then(function(response) {return response ? response.json() : null})
          .then(function(respon_data){
            if(respon_data ==null ) return
              let item = respon_data.stickers.find((item) => {return (item.id === offline_id)})
              if (item) {
                  item.name = img_name
                  item.category = category
                  if(fileName!='')
                    item.stiker_img = fileName
              }
              else
              {
                respon_data.stickers.unshift(modify_data)
              }
              const jsonResponse = new Response(JSON.stringify(respon_data), {
                  headers: {'content-type': 'application/json'}
              });
              cache.put(requestUrl, jsonResponse.clone())
              .then(function(response){
                init_Data();
              })
          })
          
      }).catch((err)=>{
          console.log('cashe_error',err);
      })
    }

  }

  const handleCatOpenClick = (data) => {
      setCatOpen(true);
      if (data) {
        setCategoryId(data);
        $http.get(base_url + "category/getCategoryInfo", {params: {id: data}})
        .then((response) => {
            if (response.data.status==="success") {
                setAddCategory(response.data.category.name);
                setAddCategoryDes(response.data.category.description);
                setCategoryImgUrl(response.data.category.category_img ? 
                    (public_url + 'pixie/assets/images/ui/' + response.data.category.category_img) : 
                    'https://via.placeholder.com/300x300');
            }
        }).catch((error) => {
          setGoback(true)
        })
      }
  }

  const handleCatCloseClick = () => {
      setCatOpen(false);
  }

  const AddCategoryClick = () => {
    handleCatCloseClick();
    let offline_id = category_id > 0 ? category_id:JSON.parse(localStorage.getItem('user')).first_name+'_'+new Date().getTime();
    const frmData = new FormData();
    frmData.append('id', offline_id);
    frmData.append('name', add_category);
    frmData.append('description', add_category_des);
    frmData.append('category_img', category_img);
    if (category_id > 0) {
      setCategoryId(0);
    }
    $http.post(base_url + "category/updateCategory", frmData)
    .then((response) => {
        if (response.data.status === "success") {
            NotificationManager.success("Update Category", "Success!", 1000, () => {
            });
            if(navigator.onLine)
              init_Data();
        } else if (response.data.status === "error") {
            NotificationManager.error(response.data.msg, "Error!", 1000, () => {
            });
        }
    }).catch((error)=>{
      NotificationManager.error(error, "Error!", 1000, () => {
      });
    })
    if (!navigator.onLine){
      caches.open('SirvezApp').then(function(cache){
          let fileName = ''
          if(category_img)
          {
              fileName = new Date().getTime()+'_new.jpeg';
              cache.put('/pixie/assets/images/ui/'+fileName, new Response(category_img));
          }
          var modify_data = {
              id : offline_id,
              name : add_category,
              description : add_category_des,
              stickers_count:0,
          }   
          if(fileName!='')
            modify_data.category_img = fileName
          //save category_edit
          cache.match('/api/category/getCategoryInfo?id='+offline_id)
          .then(function(response) {return response.json()})
          .then(function(res_data){
            res_data.catogory.name = add_category
            res_data.description = add_category_des
            if(fileName!='') res_data.catetory_img = fileName
            const j_Response = new Response(JSON.stringify(res_data), {
              headers: {
                  'content-type': 'application/json'
              }
            });
            cache.put('/api/category/getCategoryInfo?id='+offline_id, j_Response.clone());

          })
          .catch(function(err){
            let res_data = {category:modify_data,stickers :[]}
            const j_Response = new Response(JSON.stringify(res_data), {
                headers: {
                    'content-type': 'application/json',
                },
                method:"POST"
            });
            cache.put('/api/category/getCategoryInfo?id='+offline_id, j_Response.clone());
          })
            
          //save into list
          const requestUrl = '/api/category/categoryList';
          cache.match(requestUrl)
          .then(function(response) {return response.json()})
          .then(function(res_data){
              let item = res_data.category.find((item) => {return (item.id === offline_id)})
              if (item) {
                  item.name = add_category
                  item.description = add_category_des                  
                  if(fileName!='')
                    item.category_img = fileName
              }
              else
              {
                res_data.category.unshift(modify_data)
              }
              const jsonResponse = new Response(JSON.stringify(res_data), {
                  headers: {'content-type': 'application/json'}
              });
              cache.put(requestUrl, jsonResponse.clone())
              .then(function(response){
                init_Data();
              })
              
          })
          //add-api/room/editPhoto
          cache.match('/api/room/editPhoto')
          .then(function(response) {return response ? response.json() : null})
          .then(function(res_data){
            if (res_data == null) return
              let item = res_data.categories.find((item) => {return (item.id === offline_id)})
              if (item) {
                  item.name = add_category
                  item.description = add_category_des                  
                  if(fileName!='')
                    item.category_img = fileName
              }
              else
              {
                modify_data.stickers = []
                res_data.categories.unshift(modify_data)
                
              }
              const jsonResponse = new Response(JSON.stringify(res_data), {
                  headers: {'content-type': 'application/json'}
              });
              cache.put('/api/room/editPhoto', jsonResponse.clone())
              
          })
          
      }).catch((err)=>{
          console.log('cashe_error',err);
      })
    }
  }


  const changeImage = (e) =>{
    e.preventDefault();

    let reader = new FileReader();
    let file = e.target.files[0];
    reader.onloadend = () => {
        setAddImg(file);
        setImageUrl(reader.result);
    }
    reader.readAsDataURL(file)
  }

  const changeCategoryImg = (e) =>{
    e.preventDefault();

    let reader = new FileReader();
    let file = e.target.files[0];
    reader.onloadend = () => {
        setCategoryImg(file);
        setCategoryImgUrl(reader.result);
    }
    reader.readAsDataURL(file)
  }

  return (
    loader ? (

    <div>
      <DataTableToolbar  
        handleOpenClick={handleOpenClick} 
        handleCatOpenClick={handleCatOpenClick}/>
      <div className="flex-auto">
        <div className="table-responsive-material">
          <Table className="">
            <DataTableHead
              order={order}
              orderBy={orderBy}
              onRequestSort={handleRequestSort}
            />
            <TableBody>
              {category_data.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map(n => {
                return (
                  <TableRow
                    hover
                    // onClick={event => handleClick(event, n.id)}
                    //onKeyDown={event => handleKeyDown(event, n.id)}
                    tabIndex={-1}
                    key={n.id}
                  >
                    <TableCell onClick={() => handleClick(n.id)}>
                      <div className="user-profile d-flex flex-row align-items-center">
                          <Avatar
                              alt={n.name}
                              src={public_url + 'pixie/assets/images/ui/' + n.category_img }
                              
                              className="user-avatar"
                          />
                          <div className="user-detail">
                              <h5 className="user-name">{n.name}</h5>
                          </div>
                      </div>
                    </TableCell>
                    <TableCell onClick={() => handleClick(n.id)}>{n.stickers_count}</TableCell>
                    <TableCell onClick={() => handleClick(n.id)}>{n.description}</TableCell>
                    <TableCell className="text-right">
                      <CardEditMenu key={n.id} selectedData={n.id} onSelectRow={onSelectCategory}/>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
            <TableFooter>
              <TableRow>
                <TablePagination
                  count={category_data.length}
                  rowsPerPage={rowsPerPage}
                  page={page}
                  onChangePage={handleChangePage}
                  onChangeRowsPerPage={handleChangeRowsPerPage}
                />
              </TableRow>
            </TableFooter>
          </Table>
          <NotificationContainer/>
        </div>
      </div>

      <Modal key="add-img" className="modal-box" toggle={handleCloseClick} isOpen={open}>
          <ModalHeader>Add Image
            <IconButton className="text-grey"
                        onClick={handleCloseClick}>
                <CloseIcon/>
            </IconButton>
          </ModalHeader>
          <div className="modal-box-content">
            <div className="row">
              <div className="col-xl-6 col-lg-6 col-md-6 col-12">
                <div className="jr-card pb-2">
                    <div className="jr-card-thumb">
                        <img className="card-img-top img-fluid" alt="products" src={ImageUrl}/>
                    </div>
                    <input type="file" id="add_img" accept=".png" 
                        name="add_img" style={{display: "none"}}
                        onChange={(event) => changeImage(event)}/>

                    <div className="jr-card-social text-right">
                        <Fab className="jr-fab-btn bg-secondary text-white jr-btn-fab-xs mb-3" 
                            onClick={(e)=>{document.getElementById('add_img').click()}}>
                            <i className="zmdi zmdi-cloud-upload zmdi-hc-1x"/>
                        </Fab>
                    </div>
                </div>
              </div>

              <div className="col-xl-6 col-lg-6 col-md-6 col-12">
                <TextField
                    autoFocus
                    margin="dense"
                    id="name"
                    value={img_name}
                    onChange={(event)=>setImgName(event.target.value)}
                    label="Name"
                    helperText="Please Enter Name"
                    fullWidth
                />
                <TextField
                    id="category"
                    select
                    label="Category"
                    value={category}
                    onChange={(event)=>setCategory(event.target.value)}
                    SelectProps={{}}
                    helperText="Please select Category"
                    margin="normal"
                    fullWidth
                >
                    {categories.map(option => (
                        <MenuItem key={option.id} value={option.id}>
                            {option.name}
                        </MenuItem>
                    ))}
                </TextField>
                <TextField
                    id="status"
                    select
                    label="Status"
                    value={status}
                    onChange={(event)=>setStatus(event.target.value)}
                    SelectProps={{}}
                    helperText="Please select Status"
                >
                    {status_rows.map(option => (
                        <MenuItem key={option.id} value={option.id}>
                            {option.content}
                        </MenuItem>
                    ))}
                </TextField>
              </div>
            </div>
          </div>
          <div className="modal-box-footer d-flex flex-row">
            <Button onClick={()=>handleCloseClick()} color="secondary">
              Cancel
            </Button>
            <Button onClick={()=>AddImageClick()} color="primary">
              Save
            </Button>
          </div>
        </Modal>
      <Modal key="add-category" className="modal-box" toggle={handleCatCloseClick} isOpen={cat_open}>
        <ModalHeader>Add Category
          <IconButton className="text-grey"
                      onClick={handleCatCloseClick}>
              <CloseIcon/>
          </IconButton>
        </ModalHeader>
        <div className="modal-box-content">
          <div className="row">
              <div className="col-xl-6 col-lg-6 col-md-6 col-12">
                <div className="jr-card pb-2">
                    <div className="jr-card-thumb">
                        <img className="card-img-top img-fluid" alt="products" src={CategoryImgUrl}/>
                    </div>
                    <input type="file" id="category_img" accept="image/*" 
                        name="category_img" style={{display: "none"}}
                        onChange={(event) => changeCategoryImg(event)}/>

                    <div className="jr-card-social text-right">
                        <Fab className="jr-fab-btn bg-secondary text-white jr-btn-fab-xs mb-3" 
                            onClick={(e)=>{document.getElementById('category_img').click()}}>
                            <i className="zmdi zmdi-cloud-upload zmdi-hc-1x"/>
                        </Fab>
                    </div>
                </div>
              </div>
              <div className="col-xl-6 col-lg-6 col-md-6 col-12">
                  <TextField
                      autoFocus
                      margin="dense"
                      id="add_category"
                      value={add_category}
                      onChange={(event)=>setAddCategory(event.target.value)}
                      label="Name"
                      helperText="Please Enter Category Name"
                      fullWidth
                  />
                  <TextField
                      autoFocus
                      margin="dense"
                      id="add_category_des"
                      value={add_category_des}
                      onChange={(event)=>setAddCategoryDes(event.target.value)}
                      label="Description"
                      helperText="Please Enter Category Description"
                      fullWidth
                  />
              </div>
          </div>
        </div>
        <div className="modal-box-footer d-flex flex-row">
          <Button onClick={()=>handleCatCloseClick()} color="secondary">
            Cancel
          </Button>
          <Button onClick={()=>AddCategoryClick()} color="primary">
            Save
          </Button>
        </div>
      </Modal>

    </div>
    ):(
      <div className="loader-view"
            style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
        <CircularProgress/>
        <SweetAlert show={goback}
            warning
            confirmBtnText="Go Back"
            confirmBtnBsStyle="danger"
            cancelBtnBsStyle="default"
            title="Warning!"
            onConfirm={() => History.go(-1)}
        >                
        Data not cached, so you can not view this page.
        </SweetAlert>
      </div>        
    )
  );
};

export default CategoryTable;
